@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background: linear-gradient(to right bottom, #262424, #1c1414);
}

.search{
    color: #fff;
    display: flex;
    flex-direction: column;
    font-family: 'Dancing Script';
    align-items: center;
    justify-content: center;
}

.search h1{
    text-align: center;
    padding: 20px 0 0 0;
}

.form{
    position: relative;
    display: flex;
    align-items: center;
    height: 40px;
    width: 300px;
    padding: 20px 0;
}

.searchbar, .go, .search_icon{
    transition: 0.3s;
    position: absolute;
}

.searchbar{
    right: 0;
    height: 40px;
    width: 260px;
    color: #fff;
    letter-spacing: 1px;
    border-radius:  0 20px 20px 0;
    outline: none;
    border: none;
    background: linear-gradient(to right, #6b2d2d, #752626);
    box-sizing: border-box;
    caret-color: #6b2d2d;
}

.searchbar:focus, .searchbar:focus ~ .search_icon{
    transition: 0.3s;
    background: rgb(31, 27, 27);
    border-bottom: 1px solid #752626;
    border-top: 1px solid #6b2d2d;
}
.searchbar:focus ~ .go{
    transition: 0.3s;
    color: white;
    background: linear-gradient(to right, #6b2d2d, #752626);
    box-shadow: none;
}

.searchbar::placeholder{
    padding-left: 2px;
    color: rgb(224, 224, 224);
    letter-spacing: 1px;
}

.go{
    right: 5px;
    color: #752626;
    background: white;
    outline: none;
    border: none;
    width: 60px;
    height: 30px;
    border-radius: 15px;
    box-shadow: 0 0 10px 5px #742f2f;
}

.go:hover{
    transform: scale(1.25);
    cursor: pointer;
}

.go:active{
    transform: scale(1);
}

.search_icon{
    left: 0;
    background: linear-gradient(to right, #742f2f, #6b2d2d);
    z-index: 1;
    color: #fff;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50% 0 0 50%;
    box-sizing: border-box;
}

.extra1, .extra2{
    position: absolute;
    height: 40px;
    width: 0;
    background: rgb(255, 255, 255);
    opacity: 0.15;
    border-radius: 20px;
}

.extra1{
    left: 0;
}
.extra2{
    right: 0;
}

#pokeSearchBar:focus .extra1, #pokeSearchBar:focus .extra2{
    transition: 0.5s;
    width: 300px;
}

.pokemons_container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.extra{
    height: 100px;
}