@import url('https://fonts.googleapis.com/css2?family=Hind:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Acme&display=swap');
.pokemon{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 1100px;
    border-radius: 15px;
    color: white;
    font-size: 20px;
    font-family: 'Acme', sans-serif;
    text-transform: capitalize;
    letter-spacing: 1px;
    line-height: 35px;
    padding: 10px 20px;
    margin: 15px 0;
}

.pokemon *{
    margin: 0 10px;
}

.pokemon-dark{
    text-shadow: 0 0 5px #340c0c;
    background: linear-gradient(to right bottom, #340c0c, #2c1111);
}

.pokemon-light{
    text-shadow: 0 0 5px #6b2d2d;
    background: linear-gradient(to right bottom, #6b2d2d, #752626);
}

.img{
    height: 100px;
    width: 100px;
    border-radius: 50px;
    display: flex;
    justify-content: center;
}

.img-head{
    width: 100px;
}

.img-dark{
    background: linear-gradient(to right bottom, #6b2d2d, #752626);
    box-shadow: 0 0 5px 5px #2c1111;
}

.img-light{
    background: linear-gradient(to right bottom, #340c0c, #2c1111);
    box-shadow: 0 0 5px 5px #752626;
}

.name{
    width: 150px;
}
.head-name{
    width: 270px;
    border: none;
}

.weight, .height{
    width: 100px;
}

.type, .moves, .abilities{
    display: flex;
    flex-direction: column;
}

.type{
    width: 125px;
}

.moves{
    width: 175px;
}

.abilities{
    width: 175px;
}

.next{
    text-align: center;
    width: 1100px;
    margin: auto;
    color: white;
}

.next button{
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 20px;
    font-size: 16px;
    color: white;
    padding: 10px 20px;
    font-family: 'Acme', sans-serif;
    background: linear-gradient(to right bottom, #340c0c, #2c1111)
}

.next button:hover{
    transition: 0.3s;
    background: linear-gradient(to right bottom, #6b2d2d, #752626);
    transform: scale(1.1);
}

.next button svg{
    position: relative;
    top: 2px;
}